
.modal {
    position: absolute;
    width: 200px;
    background-color: #fff;
    top: -20px;
    left: calc(50% - 100px);
    padding: 10px 5px;
    text-align: center;
    font-size: 24px;
    background-color: rgb(238, 200, 200);
    border: 2px solid rgb(97, 97, 97);
    border-radius: 8px;
}